
import {StoreBase} from "shared/core/Store/StoreBase";
import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";



export class FBDateRangeOptions extends FBFieldWithPriceBaseOptions{

    public Placeholder:string;
    public ShowQuantitySelector:boolean;
    public Format:string;
    public Orientation:'horizontal'|'vertical';
    public StartDate:DateSectionOption;
    public EndDate:DateSectionOption;


    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Type=FieldTypeEnum.DateRange;
        this.Format='dd/MM/yyyy';
        this.Orientation='horizontal';

        this.StartDate=new DateSectionOption().Merge({Label:'Start Date'});
        this.EndDate=new DateSectionOption().Merge({Label:'End Date'});
    }
}


export class DateSectionOption extends StoreBase{
    DefaultDate:string;
    Placeholder:string;
    Label:string;

    protected LoadDefaultValues() {
        this.DefaultDate='';
        this.Placeholder='';
        this.Label='';

    }


}