import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";
import {FBDateRangeOptions} from "./FBDateRange.Options";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
let moment = require("moment");
import {PricePerDayCalculator} from "./PricePerDayCalculator";

let format=require('date-fns').format;
export class FBDateRangeModel extends FBFieldWithPriceBaseModel<FBDateRangeOptions,FBColumnModel>
{
    public StartDate:number;
    public EndDate:number;
    public DateFormat:string;

    constructor(Options: FBDateRangeOptions, Parent: FBColumnModel) {
        super(Options, Parent);
    }


    protected InitializePriceCalculator() {
        if(this.Options.PriceType==PriceTypeEnum.price_per_day)
        {
            this.calculator=new PricePerDayCalculator().Initialize(this);
        }else {
            super.InitializePriceCalculator();
        }
    }

    protected InternalSerialize(obj: any) {
        super.InternalSerialize(obj);
        let value=this.GetValue();
        if(value==null)
            return;


        obj.Value={};
        obj.Value.StartDateLabel=this.Options.StartDate.Label;
        obj.Value.EndDateLabel=this.Options.EndDate.Label;
        obj.Value.StartUnix=value.StartDate;
        obj.Value.EndUnix=value.EndDate;

        let date=new Date(obj.Value.StartUnix*1000);
        date=new Date(date.setMinutes(date.getMinutes()+date.getTimezoneOffset()));
        obj.Value.StartValue=format(date,this.DateFormat);

        date=new Date(obj.Value.EndUnix*1000);
        date=new Date(date.setMinutes(date.getMinutes()+date.getTimezoneOffset()));
        obj.Value.EndValue=format(date,this.DateFormat);
    }



    GetStoresInformation(): boolean {
        return true;
    }

    GetIsUsed()
    {
        return this.GetValue()!=null;
    }

    GetValue() {
        if(!this.GetIsVisible()||this.StartDate==0||this.EndDate==0)
           return null;
        return {
            StartDate:this.StartDate,
            EndDate:this.EndDate
        };
    }

    CalculateDefaultDate(property:'StartDate'|'EndDate',defaultDate:string){
        this[property]=0;
        let dateToUse:Date=null;
        if(defaultDate.indexOf('/')>=0)
        {
            let dateParts=defaultDate.split('/');
            if(dateParts.length==3)
            {
                let year=parseInt(dateParts[0]);
                let month=parseInt(dateParts[1]);
                let day=parseInt(dateParts[2]);
                if(!isNaN(year)&&!isNaN(month)&&!isNaN(day))
                {
                    month--;
                    dateToUse=new Date(year,month,day);
                }
            }
        }else{
            if(defaultDate.trim()!='')
            {
                let numberOfDays=parseFloat(defaultDate.trim());
                dateToUse=new Date();
                dateToUse.setHours(0);
                dateToUse.setMinutes(0);
                dateToUse.setMilliseconds(0);
                dateToUse.setDate(dateToUse.getDate()+numberOfDays);
            }
        }


        if(dateToUse!=null)
        {
            let date=moment(dateToUse);
            this[property]=moment(date).add(date.utcOffset(), 'm').unix();
        }






    }

    GetDynamicFieldNames(): string[] {
        return ['FBDateRange'];
    }

    public CalculateDateFormat() {
        this.DateFormat='MM/dd/yyyy';
        try{
            format(new Date(),this.Options.Format);
        }catch(e )
        {
            return;
        }

        this.DateFormat=this.Options.Format;
    }

    InitializeStartingValues() {
        this.CalculateDefaultDate("StartDate", this.Options.StartDate.DefaultDate);
        this.CalculateDefaultDate("EndDate", this.Options.EndDate.DefaultDate);

        let value=this.GetPreviousDataProperty('Value', null);
        this.StartDate=0;
        this.EndDate=0;
        if(value!=null&&value.StartUnix!=null)
            this.StartDate=value.StartUnix;

        if(value!=null&&value.EndUnix!=null)
            this.EndDate=value.EndUnix;


        this.DateFormat='';

        this.Quantity=this.GetPreviousDataProperty( 'Quantity',this.Options.QuantityDefaultValue==''?"": this.ParseNumber(this.Options.QuantityDefaultValue));
        this.CalculateDateFormat();
    }
}